



























import {
	bankCardAttribution
} from '../../common/checkCard.js';
export default {
	name: 'checkCard',
	data() {
		return {
			Info: '',
			PersonName: '',
			addInfo: ''
		}
	},
	onLoad(params) {
		if (params.cardInfo) {
			var cardInfo = JSON.parse(params.cardInfo);
			this.PersonName = cardInfo.card_name;
			this.Info = cardInfo.card_num;
		}
	},
	methods: {
		massage() {
			// 			bankCode:"CIB",
			// 			bankName:"兴业银行",
			// 			cardType:"DC"
			// 			cardTypeName:"储蓄卡"
			var allIma = bankCardAttribution(this.Info);
			if (allIma == 'error') {
				uni.showModal({
					// title: '提示',
					content: '卡号输入有误，请重试？',
					success: (res) => {
						if (res.confirm) {
							this.Info = '';
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else {
				allIma.PersonName = this.PersonName;
				allIma.Info = this.Info;
				this.addInfo = JSON.stringify(allIma);
				uni.redirectTo({
					url: './addCard_finish?addInfo=' + this.addInfo
				});
			}
		},
	}
}
